// react
import React, { useEffect } from "react";
import { Link } from "react-router-dom";
//  AOS  (animaciones) 
import AOS from 'aos';
import "aos/dist/aos.css";
// components
import BaseLayout from "./layout";
// STYLES
import "../styles/nosotros/css/nosotros.css"
// IMAGENES
import title from "../styles/home/img/caeii-title.png"
import cohete from "../styles/home/img/cohete-botones.svg"
import que_es_caeii from "../styles/nosotros/img/Qu es CAEII.jpg"
import Que_incluye from "../styles/nosotros/img/Que_incluye.png"
import que_es_aareii from "../styles/home/img/logo-aareii-desktop-textogrande.png"
import que_son_los_pilares from "../styles/nosotros/img/QUE SON LOS PILARES.jpg"
import cuando_donde from "../styles/nosotros/img/cUANDO Y DONDE.jpg"
import como_participar from "../styles/nosotros/img/Como participar.jpg"
import cual_es_su_objetivo from "../styles/nosotros/img/Objetivos.jpeg"


export default function Nosotros() {
  useEffect(() => {
    AOS.init({
      duration: 1500, // values from 0 to 3000, with step 50ms
      once: true, // whether animation should happen only once - while scrolling down
    });
  }, []);
  return (
    <div className="App" id="nosotros">
      <BaseLayout>
        <main className="nosotros" id="home">
          <div class="title">
            <img src={title} alt="Logo caeii"></img>
            <h1 className="h1_title_desktop" > Orbitando hacia el futuro... </h1>
          </div>
          <h1 className="h1_title_mobile"> Orbitando hacia el futuro... </h1>
          <section id="que_es_caeii" class="nosotros_section reverse">
            <div class="info" data-aos="fade-up">
              <h2> Qu es CAEII? </h2>
              <p>
                El Congreso Argentino de Estudiantes
                de Ingeniera Industrial y carreras afines
                (CAEII) es el evento ms importante de AArEII. Tiene
                una duracin de cuatro das, donde se disfrutan y
                aprovechan diversas actividades acadmicas. El
                mismo tiene lugar en el mes de agosto de cada ao y
                cuenta con una asistencia de 1500 estudiantes
                aproximadamente.
              </p>
            </div>
            <div className="nosotros_img_conteiner"  data-aos="fade-up">
              <img src={que_es_caeii} className="img_representativa" alt="imagen representativa"/> 
            </div>
          </section>
          <section id="Que_incluye" class="nosotros_section">
            <div class="info" data-aos="fade-up">
              <h2> Qu incluye? </h2>
              <p>
                El evento incluye no solo talleres, visita tcnica, charlas, seguro y coffee, sino tambin cocktail de bienvenida,
                merchandising, cena de gala, sorteos, noches de boliche y mucho ms.
                <br/><br/>
                <b>*No incluye gastos no especificados en la imagen.</b>
              </p>
            </div>
            <div className="nosotros_img_conteiner" data-aos="fade-up">
              <img src={Que_incluye} className="img_representativa" alt="imagen representativa"/>
            </div>
            
          </section>
          <section id="cual_es_su_objetivo" class="nosotros_section reverse">
            <div class="info" data-aos="fade-up">
              <h2> Cul es su objetivo? </h2>
              <p>
                En el CAEII se busca promover el crecimiento
                y la formacin integral del estudiante, ofrecindole herramientas para que, en un futuro,
                pueda desenvolverse de la mejor manera al
                afrontar situaciones
                reales del mundo laboral. Entre las
                principales actividades
                que se realizan, se
                pueden destacar conferencias, talleres
                y visitas tcnicas,
                entre otras.
              </p>
            </div>
            <div className="nosotros_img_conteiner" data-aos="fade-up">
              <img src={cual_es_su_objetivo} className="img_representativa" alt="imagen representativa"/>
            </div>
          
          </section>
          <section id="que_es_aareii" class="nosotros_section">
            <div class="info" data-aos="fade-up">
              <h2> Qu es AArEII? </h2>
              <p>
                Asociacin nacional sin fines de lucro formada e integrada por estudiantes de ingeniera. La
                misma cuenta con presencia en todo el pas, en ms de
                40 Universidades Activas, las cuales poseen representantes que coordinan cada delegacin.
              </p>
              <div class="linea">
                <a href="https://www.aareii.org.ar/">{" Conoc ms >"}</a>
                <div class="line_container"><div class="line"></div></div>
              </div>
            </div>
            <div className="nosotros_img_conteiner"  data-aos="fade-up">
              <img src={que_es_aareii} className="img_representativa que_es_aareii" alt="imagen representativa"/>
            </div>
          
          </section>
          <section id="que_son_los_pilares" class="nosotros_section reverse">
            <div class="info"  data-aos="fade-up">
              <h2> Qu son los pilares? </h2>
              <p>
                Son los ejes temticos que se abordarn en el evento. Todos los CAEII se centran en 4 pilares diferentes a travs de charlas, talleres y conferencias, entre otros. Los pilares de este ao son:
              </p>
              <div class="links">
                <div class="link">
                  <div class="linea">
                    <Link to="/info/Estaciones">{" Logstica 4.0 >"}</Link>
                    <div class="line_container"><div class="line"></div></div>
                  </div>
                </div>
                <div class="link">
                  <div class="linea">
                    <Link to="/info/Estaciones">{" Odisea Espacial >"}</Link>
                    <div class="line_container"><div class="line"></div></div>
                  </div>
                </div>
                <div class="link">
                  <div class="linea">
                    <Link to="/info/Estaciones">{" Ingeniera del maana >"}</Link>
                    <div class="line_container"><div class="line"></div></div>
                  </div>
                </div>
                <div class="link">
                  <div class="linea">
                    <Link to="/info/Estaciones">{" Desarrollo Sustentable: Smart City >"}</Link>
                    <div class="line_container"><div class="line"></div></div>
                  </div>
                </div>
              </div>
            </div>
            <div className="nosotros_img_conteiner"  data-aos="fade-up">
              <img src={que_son_los_pilares} className="img_representativa" alt="imagen representativa"/>
            </div>
            
          </section>
          <section id="cuando_donde" class="nosotros_section">
            <div class="info"  data-aos="fade-up">
              <h2> Cundo y dnde? </h2>
              <p>
                En la ciudad de Mendoza, desde el 25 hasta el 28 de agosto. Se desarrollarn varias actividades en diferentes lugares de la ciudad.
              </p>
              <h2> Precio? </h2>
              <p>
                El precio para la segundo fecha de inscripcin es de $10.990. El segundo periodo de inscripcin ser del 11 julio a las 8:00 hs al 18 julio a las 20:00 hs
                <br/> <strong> solo hay 500 cupos disponibles </strong>.
              </p>
            </div>
            <div className="nosotros_img_conteiner"   data-aos="fade-up">
              <img src={cuando_donde} className="img_representativa" alt="imagen representativa"/>
            </div>
            
          </section>
          <section id="como_participar" class="nosotros_section reverse">
            <div class="info"  data-aos="fade-up">
              <h2> Cmo participar? </h2>
              <p>
                Inscribite ahora y reserv un lugar en el mejor congreso del ao!
              </p>
              <div class="button">
                <a href="https://inscripciones.aareii.org.ar/index.php/register" class="hyperspan">  
                  <div class="circle">        
                    <div class="rocket_container">
                      <img src={cohete} alt="cohete"></img>
                    </div>
                    <div class="circle2"></div>
                  </div>
                  <div class="texto"> REGISTRARSE </div>
                </a>
              </div>
            </div>
            <div className="nosotros_img_conteiner"  data-aos="fade-up">
              <img src={como_participar} className="img_representativa" alt="imagen representativa"/>
            </div>
          
          </section>
          {/* 
          <section id="saber_mas" class="nosotros_section">
            <div class="info"  data-aos="fade-up">
              <h2> Quers saber ms del evento? </h2>
              <p>
                Pods volver a la home y leer todo sobre las actividades que podrs disfrutar durante todo el evento.
              </p>
              <div class="button">
                <Link to="/home" class="hyperspan">  
                  <div class="circle">        
                    <div class="rocket_container">
                      <img src={cohete}  alt="cohete"></img>
                    </div>
                    <div class="circle2"></div>
                  </div>
                  <div class="texto"> volver al home </div>
                </Link>
              </div>
            </div>
            <img src={placeholder} alt="imagen representativa" data-aos="fade-up"/>
          </section>
          <section id="sponsors">
            <h2> Sponsors </h2>
            <div class="imgs">
              <img src="img/home/sponsors/panAmerican.png" alt="Pan American Energy"/>
              <img src="img/home/sponsors/UNCUYO.png" alt="universidad nacional de cuyo"/>
              <img src="img/home/sponsors/UM.png" alt="Universidad de Mendoza"/>
            </div>
          </section> */}
        </main>
      </BaseLayout>
    </div>
  )
}